.header-nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login-button{
    display: flex;
    align-items: center;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background-color: var(--blue);
    gap: 5px;
}

.login-button span{
    color: #fff;
}

.header__back{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    box-shadow: 0px 4px 26px 0px #00000014;
    background-color: #fff;
}