.main {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
}

.main-content {
    margin-top: auto;
    margin-bottom: auto;
}

.main-days {
    width: 850px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.main-day-wrapper {
    padding: 2px;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 99px;
    border-radius: 50px;
}

.main-day {
    background: linear-gradient(0deg, rgba(68, 21, 0, 0.03), rgba(68, 21, 0, 0.03)),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    height: 95px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    border-radius: 48px;
    gap: 15px;
    width: 100%;
}

.main-day__pic {
    position: relative;
    width: 53px;
    height: 53px;
    display: flex;
    align-items: center;
    border-radius: 50%;
    background-color: var(--blue);
    flex-shrink: 0;
}

.main-day__pic img {
    position: absolute;
    left: -10px;
    /* right: 0; */
    bottom: -5px;
}

.day-info {
    width: 100%;
    height: 55px;
    border-radius: 28px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
    padding-left: 15px;
    /* padding-right: 15px; */
    /* flex-shrink: 0; */
    /* width: 490px; */
    background-color: #00000012;
}

.day-info_active {
    background-color: #fff;
}

.day-info__text span {
    color: #00000099;
}

.day-info__check {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #0000002B;
    border-radius: 15px;
    background-color: #fff;
}

.day-info__check_active {
    background-color: #E73F0C;
    border: 2px solid #0000002B;
}

.main-day__button {
    width: 230px !important;
    flex-shrink: 0;
}

.day-timer {
    width: 385px;
    height: 55px;
    display: flex;
    align-items: center;
    background: #FFFFFF0F;
    border-radius: 28px;
    margin-left: auto;
    padding-left: 25px;
    padding-right: 25px;
    justify-content: space-between;
}

.day-timer__text {
    margin-right: auto;
    margin-left: 10px;
    font-size: 14px;
    color: #ffffff88;
}

.day-timer__time {
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
}

.to-task-button>span {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.to-task-button__icon {
    background-color: #fff;
    border-radius: 18px;
    height: 35px;
    width: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0px;
    border: 2px solid #F9F4EC;
}

.task-done-button>span {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 100%;
}


