.footer {
    background-color: var(--orange);
    position: relative;
    padding-top: 20px;
    padding-bottom: 50px;
}

.footer::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 100%;
    width: 100%;
    height: 10vw;
    background-image: url(../../../public/assets/images/footer-shape.svg);
    background-repeat: no-repeat;
    background-size: calc(100% + 2px);
    background-position: center calc(100% + 2px);
}

.footer-content {
    display: flex;
    gap: 75px;
}

.footer__logo {
    display: flex;
    margin-bottom: 10px;
}

.copyright {
    color: #fff;
    font-size: 14px;
}

.footer-menu {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    width: 840px;
    gap: 13px 55px;
}

.footer-menu__link {
    font-size: 14px;
    font-weight: 500;
    color: #fff;
}