:root {
    --black: #000000;
    --orange: #F3410E;
    --main-bg: #F9F4EC;
    --blue: #35A2EB;
}

#root {
    background-color: var(--main-bg);
    /* min-height: 100dvh; */
}

* {
    outline: none !important;
    box-sizing: border-box !important;
    margin: 0;
}

ul,
ol,
p,
div,
label,
h1,
h2,
h3,
h4,
h5 {
    list-style: none;
    padding: 0;
    margin: 0;
}

button,
button span {
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 0;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

img {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
    user-select: none;
}

a,
a:hover {
    text-decoration: none;
}

body,
html {
    font-family: 'Montserrat', sans-serif;
    /* min-height: 100vh; */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    scroll-behavior: smooth;
}

.grecaptcha-badge {
    display: none !important;
}

.main {
    background-color: var(--main-bg);
    /* position: relative; */
}

.container {
    max-width: 1300px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}