.main {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
}

.main-content {
    margin-top: auto;
    margin-bottom: auto;
}

.day-wrapper {
    background-color: #fff;
    padding: 30px;
    border-radius: 80px 80px 0 0;
}

.day-header {
    background: linear-gradient(0deg, rgba(68, 21, 0, 0.03), rgba(68, 21, 0, 0.03)),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    height: 95px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 48px;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 65px;
}

.day-header__day {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    border-radius: 20px;
    box-shadow: 0px 4px 4px 0px #FFFFFF54 inset, 0px 12px 26.7px 0px #F3410E91;
    background: linear-gradient(0deg, #E73F0C, #E73F0C),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%),
        linear-gradient(0deg, #F3410E, #F3410E);
    width: 100px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
}

.day-header__counter {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    border-radius: 20px;
    box-shadow: 0px 4px 15px 0px #0000000D;
    background-color: #fff;
    width: 100px;
    color: #000;
    font-size: 14px;
    font-weight: 500;
}

.day-items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px;
}

.day-item {
    padding: 2px;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    border-radius: 30px;
}

.day-item__content {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #F9F4EC;
    border-radius: 30px;
    align-items: center;
}

.day-item__pic-wrapper {
    position: relative;
    width: 150px;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    padding: 3px;
    margin-bottom: 20px;
}

.day-item__pic {
    background: radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 10px solid #fff;
    position: relative;
}

.day-item__pic img {
    position: absolute;
    left: -10px;
    right: 0;
    margin: auto;
    width: 144px;
    height: 144px;
    object-fit: contain;
    object-position: center;
    /* right: 0; */
    /* margin: auto; */
    bottom: 15px;
}

.day-item__title {
    text-align: center;
    margin-bottom: 20px;
}